    type: 'info',
    html: 'Note that <b>context</b> as it is known in <b>v3</b> was in <b>v2</b> ' + 'bound to the function ```this``` context. This made arrow functions ' + 'unsupported for passing context. In <b>v3</b> ```resolve``` functions ' + 'are no longer bound to context and instead are passed a merged context value.'
