    html: 'These documents assume you are already familiar with graphql ' + 'concepts. If you are not, it is recommended that you first review ' + 'the <a href="http://graphql.org/learn/", target="_blank">learn</a> ' + 'section on the <a href="http://graphql.org/", target="_blank">graphql ' + 'project page</a>.'
